.card1{
    width: 250px;
    height: 350px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    position: relative;
    /*background-color: */
    /**/
    z-index: 0;
}

.card1 h1{
    width: 100%;
    height: 50%;
    font-size: 5em;
    display: grid;
    place-items: center;
    z-index: 3;

}

.card1 p{
    width: 100%;
    height: 50%;
    font-size: 2em;
    display: grid;
    place-items: center;
    z-index: 3;


}



.card1::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 50%;
    background-color: rgba(160, 160, 160, 0.1);
    z-index: -1;
}

.card1::after{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(55, 148, 49,0.7);
    -webkit-clip-path: circle(50% at 50% 10%);
            clip-path: circle(50% at 50% 10%);
    z-index: -2;
    transition: .5s;
}

.card1:hover::after{
    -webkit-clip-path: circle(100% at 50% 50%);
            clip-path: circle(100% at 50% 50%);

}

.card1:hover{
    color: white;
}

